import React, { useState, useContext } from "react";
import Confetti from "react-confetti";
import useWindowSize from "react-use/lib/useWindowSize";


interface Props {
    duration?: number;
    runCycle?: boolean;
}

const ConfettiEffect: React.FC<Props> = (props: Props) => {
    const [active, setActive] = useState(true);
    const { width, height } = useWindowSize();
    const widheiparams: any = {
      width,
      height,
    };
    const dur: any = props.duration ? props.duration : 2000;
    const recycle: any = props.runCycle != undefined ? props.runCycle : active;
    
    setTimeout(() => setActive(false), dur);
    return <Confetti id="confetti-effect" {...widheiparams} tweenDuration={1000} recycle={recycle} />;
  };


  export default ConfettiEffect;