import {Filesystem, Directory} from '@capacitor/filesystem';
import React from "react";
import styled from "styled-components";
import { IonButton, IonIcon } from "@ionic/react";
import { cloudUploadOutline } from "ionicons/icons";

const Button = styled.button`
  /* Insert your favorite CSS code to style a button */
`;

interface ButtonProps {
  handleFile?: any;
}

const createCacheFolder = async () => {
  await Filesystem.mkdir({
    directory: Directory.Cache,
    path: `CACHE-SOUND`
  })
}



const FileUploader: React.FC<ButtonProps> = (props) => {

  React.useEffect(() => {
    createCacheFolder();
  },[]);

  // Create a reference to the hidden file input element
  const hiddenFileInput: any = React.useRef(null);

  // Programatically click the hidden file input element
  // when the Button component is clicked
  const handleClick = (event: any) => {
    hiddenFileInput.current.click();
  }; // Call a function (passed as a prop from the parent component)

  const getBase64 = (file: any, cb: any) => {
    let reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function () {
      cb(reader.result);
    };
    reader.onerror = function (error) {
      console.log("Error: ", error);
    };
  };

  const handleChange = (event: any) => {
    const fileUploaded = event.target.files[0];
    console.log("Files", fileUploaded);
    let name = fileUploaded.name;
    const fileName = name.replace(/\.[^/.]+$/, "");
    console.log("File Name ==> ", fileName);

    getBase64(fileUploaded, (res: any) => {
      // console.log("files base64", res);
      const data: any = {
        name: fileName,
        fileName: name,
        base64: res,
      };
      // let audio: any = new Audio(res);
      // audio.play();

      localStorage.setItem("sounds", data);
      let getSnd: any = localStorage.getItem("sounds");
      console.log("getItem => ", getSnd);

      // const fileContents = new Buffer(getSnd, "base64");
      // fs.writeFile("file.mp3", fileContents, (err) => {
      //   if (err) return console.error(err);
      //   console.log("file saved to ");
      // });
    });
  };

  return (
    <>
      <IonButton
        shape="round"
        expand="block"
        fill="outline"
        onClick={handleClick}
      >
        Upload
        <IonIcon slot="end" icon={cloudUploadOutline} />
      </IonButton>
      <input
        type="file"
        ref={hiddenFileInput}
        onChange={handleChange}
        style={{ display: "none" }}
      />
    </>
  );
};

export default FileUploader;
