import React from "react";
import {
  IonContent,
  IonHeader,
  IonPage,
  IonTitle,
  IonToolbar,
} from "@ionic/react";
import ExploreContainer from "../components/ExploreContainer";
import Wheel from "../components/wheel/index";
import { PopOver } from "../components/popOver/index";
import "./Home.css";

export var ColorDet: any = React.createContext(null);
export var SoundDet: any = React.createContext(null);

export const Home: React.FC = () => {
  const onsel: any = {};
  const places: any = [
    "Pizzas",
    "Sandwiches",
    "Salads",
    "Soup",
    "Japanese food",
    "Pastas",
    "Chips",
  ];

  const changeColor = () => {};
  const [context, setContext] = React.useState("#da3768");
  const [sound, setSound] = React.useState("");

  return (
    <ColorDet.Provider value={[context, setContext]}>
      <SoundDet.Provider value={[sound, setSound]}>
        <IonPage>
          <IonHeader>
            <IonToolbar>
              <IonTitle>Spinner</IonTitle>
            </IonToolbar>
          </IonHeader>
          <IonContent fullscreen>
            <IonHeader collapse="condense">
              <IonToolbar>
                <IonTitle size="large">Blank</IonTitle>
              </IonToolbar>
            </IonHeader>
            <Wheel onSelectItem={onsel} items={places} />
            <PopOver onClickProp={() => changeColor()} />
          </IonContent>
        </IonPage>
      </SoundDet.Provider>
    </ColorDet.Provider>
  );
};
