import React, { useEffect, useContext } from "react";
import { IonToast, IonButton, useIonToast } from "@ionic/react";
import { ColorDet, SoundDet } from "../../pages/Home";
import Confetti from "../confetti/index";
import ReactAudioPlayer from "react-audio-player";
// import countdown from "../../../public/assets/audios/countdown.mp3";
import "./index.css";
// const countdownSoundUrl = require("../../../public/assets/audios/countdown.mp3");

type CustomValue = any;
interface Props {
  items: CustomValue;
  onSelectItem: CustomValue;
}

interface DefinedState {
  selectedItem: any;
  toastMsg: string;
  showToast: boolean;
}

type State = DefinedState;

const Wheel: React.FC<Props> = (props: Props) => {
  const [toast, dismiss] = React.useState<boolean>(true);
  let [selectedItem, setselectedItem]: any = React.useState(null);
  let [toastMsg, settoastMsg] = React.useState(
    "Your settings have been saved."
  );
  let [showToast, setshowToast] = React.useState(false);
  let [runConfetti, setrunConfetti] = React.useState(true);

  const { items } = props;
  const [context, setContext] = useContext(ColorDet);
  const [sound] = useContext(SoundDet);

  const [play, setPlay] = React.useState<boolean>(false);
  // const audio = new Audio(
  //   "../../../public/assets/audios/countdown.mp3"
  // );

  const soundUrl = sound;

  const wheelColor: object = {
    "--wheel-color": context,
  };

  const wheelVars: object = {
    "--nb-item": items.length,
    "--selected-item": selectedItem,
    wheelColor,
  };

  const spinning = selectedItem !== null ? "spinning" : "";

  const SelectItem = () => {
    setrunConfetti(false);
    if (selectedItem === null) {
      setPlay(true);
      const selectedItem = Math.floor(Math.random() * props.items.length);
      // if (props?.onSelectItem) {
      //   props.onSelectItem(selectedItem);
      // }
      setselectedItem(selectedItem);
      // audio.play();
      setTimeout(() => {
        settoastMsg(props.items[selectedItem]);
        setshowToast(true);
        dismiss(true);
        setPlay(false);
        // audio.pause();
      }, 4100);
    } else {
      setselectedItem(null);
    }
  };

  useEffect(() => {
    if (selectedItem == null) {
      setPlay(true);
      setshowToast(false);
      dismiss(false);
      setTimeout(() => SelectItem(), 500);
    }
  }, [selectedItem]);

  return (
    <>
      <div id="main-wheel-container" className="main-wheel-container">
        <div
          id="wheel-container"
          className="wheel-container"
          style={wheelColor}
        >
          <div
            className={`wheel ${spinning}`}
            style={wheelVars}
            onClick={SelectItem}
          >
            {items.map((item: string, index: number) => {
              const styl: any = { "--item-nb": index };
              return (
                <div
                  className="wheel-item"
                  key={index}
                  id={`spin-${index + 1}`}
                  style={styl}
                >
                  {item}
                </div>
              );
            })}
          </div>
        </div>
      </div>
      {play ? (
        <>
          <ReactAudioPlayer src={soundUrl} autoPlay={true} volume={1} />
        </>
      ) : null}
      {showToast ? (
        <>
          <Confetti duration={2500} runCycle={runConfetti} />
          <IonToast
            id="toast-msg"
            isOpen={toast}
            position="bottom"
            message={`Your got a ${toastMsg}`}
            onDidDismiss={() => dismiss(false)}
            // duration={7000}
            color="transparent"
            cssClass="toastCss"
            translucent={true}
            buttons={[
              {
                text: "Close",
                role: "cancel",
              },
            ]}
          />
        </>
      ) : null}
    </>
  );
};

export default Wheel;
