import React, { HTMLAttributes } from "react";
import { useHistory } from "react-router-dom";
import "./index.scss";

interface FluidProps {
  type?: string;
  label?: string;
  style?: object;
  id?: string;
}

const FluidInput: React.FC<FluidProps> = (props: FluidProps) => {
  const history = useHistory();
  const [username, setUsername] = React.useState<string>("");
  const [pass, setPass] = React.useState<string>("");
  const [usrValidate, setusrValidate] = React.useState<string>("");
  const [passValidate, setpassValidate] = React.useState<string>("");
  const [showError, setshowError] = React.useState<boolean>(false);

  const login = () => {
    if (!username) {
      setusrValidate("isError");
    }
    if (!pass) {
      setpassValidate("isError");
    }

    if (username == "admin" && pass == "admin@123") {
      setshowError(true);
      history.push("/");
    } else {
      setshowError(true);
    }
  };

  return (
    <div className="main">
      <p className="sign textAlign-center">Sign in</p>
      <form className="form1">
        <input
          className={`un textAlign-center ${usrValidate}`}
          type="text"
          placeholder="Username"
          onChange={(e) => setUsername(e.target.value)}
          value={username}
        />
        <input
          className={`pass textAlign-center ${passValidate}`}
          type="password"
          placeholder="Password"
          onChange={(e) => setPass(e.target.value)}
          value={pass}
        />
        {showError && (
          <div className="validate">
            <span>Please enter a valid login credentials!</span>
          </div>
        )}
        <a className="submit textAlign-center" onClick={login}>
          Sign in
        </a>
      </form>
    </div>
  );
};

interface LoginProps {}

const LoginContainer: React.FC<LoginProps> = () => {
  const style: object = {
    margin: "15px 0",
  };

  const login = () => {};

  return (
    <div className="login-container">
      <FluidInput style={style} />
    </div>
  );
};

export default LoginContainer;
